.html5-video-player {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  width: 640px;
  height: 360px;
  font-size: 11px;
  /* set the font-size of html5 player*/ }

.vop-video-container {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: black;
  z-index: 1; }

.vop-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.vop-caption-container {
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  pointer-events: none; }

.vop-ads-container {
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent; }
